              </v-row>
            </v-card-title>
            <v-card-actions v-if="exportContent === 'content1'">
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" text @click="exportToImage">Export to png</v-btn>
            </v-card-actions>
            <v-card-text v-if="exportContent== 'content1'">
              <div ref="exportContent" >
                <span v-if="kroky[0]">
                  <div v-for="(cast, index) in kroky[0].split(':')" :key="index" style="display: inline-block;">
                    <latex :latex-string="`{${cast.trim()}}${index < kroky[0].split(':').length - 1 ? '\\: : \\:' : ''}`" style="display: inline-block;"/>
                  </div>
                </span>
                <div  class="output-row" v-for="index2 in Array.from({length: pocetKrokov+1}, (_, i) => i)" :key="index2">
                  <span style="display: inline-block">
                    <latex :latex-string="`\\alpha_{${index2}} = \\langle {${prvaCastPrekladu[index2]}}`" style="display: inline-block;"/>
                    <span v-if="pocetInstrukciiPrvyKrok[index2] > 4" style="cursor: pointer;display: inline-block;"> ... </span>
                    <latex v-if="index2 < pocetKrokov" :latex-string="`, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}}\\rangle \\Rightarrow \\alpha_{${index2+1}}`" style="display: inline-block;"/>
                    <latex v-if="index2 >= pocetKrokov" :latex-string="`, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}} \\rangle`" style="display: inline-block;"/>
                  </span> 
                </div>
              </div>
            </v-card-text>
            <v-card-text v-if="exportContent=='content2'">
              <div v-for="(krok, index) in kroky" :key="index">
                {{ krok }}
                <v-divider></v-divider>
              </div>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-row>
      <v-row class="controls">
        <v-col cols="9" sm="6">
          <v-btn variant="tonal" v-if="showStepping" @click="previousStep" style="margin-right: 10px;">Naspäť</v-btn>
          <v-btn variant="tonal" v-if="showStepping" @click="nextStep">Ďalej</v-btn>
        </v-col>
        <v-col cols="3" sm="3">
          <v-btn variant="elevated" v-if="showOutput" @click="showExportPopup">EXPORT</v-btn>
        </v-col>
      </v-row>
      <v-row class="output-container" v-if="showOutput && showFullOutput">
        <v-btn v-if="showFullOutput" icon @click="fullscreenDialog = true" style="margin-left: 10px;">
            <v-icon>mdi-fullscreen</v-icon>
        </v-btn>
        <div class="output-row" v-for="index2 in Array.from({length: pocetKrokov+1}, (_, i) => i)" :key="index2">
          <span style="display: inline-block">
            <latex v-if="!showFullLine[index2]" :latex-string="`\\alpha_{${index2}} = \\langle {${prvaCastPrekladu[index2]}}`" style="display: inline-block;"/>
            <span v-if="!showFullLine[index2] && pocetInstrukciiPrvyKrok[index2] > 4" @click="toggleText(index2)" style="cursor: pointer;display: inline-block;"> ... </span>
            <latex v-if="!showFullLine[index2] && index2 < pocetKrokov" :latex-string="`, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}}\\rangle \\Rightarrow \\alpha_{${index2+1}}`" style="display: inline-block;"/>
            <latex v-if="!showFullLine[index2] && index2 >= pocetKrokov" :latex-string="`, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}} \\rangle`" style="display: inline-block;"/>

            
            <latex v-if="showFullLine[index2] && index2 < pocetKrokov" :latex-string="`\\alpha_{${index2}} = \\langle {${prvaCastPrekladu[index2] + ' : ' + druhaCastPrekladu[index2]}}, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}} \\rangle \\Rightarrow \\alpha_{${index2+1}}`" style="display: inline-block;"/>
            <latex v-if="showFullLine[index2] && index2 >= pocetKrokov" :latex-string="`\\alpha_{${index2}} = \\langle {${prvaCastPrekladu[index2] + ' : ' + druhaCastPrekladu[index2]}}, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}} \\rangle`" style="display: inline-block;"/>
          </span> 
        </div>
      </v-row>
      <v-row ref="outputContainer" class="output-container" v-if="showOutput && showStepping">
        <div class="output-row" v-for="index3 in Array.from({length: currentIndex + 1}, (_, i) => i)" :key="index3">
          <span style="display: inline-block">
            <latex v-if="!showFullLine[index3]" :latex-string="`\\alpha_{${index3}} = \\langle {${prvaCastPrekladu[index3]}}`" style="display: inline-block;"/>
            <span v-if="!showFullLine[index3] && pocetInstrukciiPrvyKrok[index3] > 4" @click="toggleText(index3)" style="cursor: pointer;display: inline-block;"> ... </span>
            <latex v-if="!showFullLine[index3] && index3 < pocetKrokov" :latex-string="`, {${zasobnikAStav[index3]}}, s_{${stavKazdyKrok[index3]}}\\rangle \\Rightarrow \\alpha_{${index3+1}}`" style="display: inline-block;"/>
            <latex v-if="!showFullLine[index3] && index3 >= pocetKrokov" :latex-string="`, {${zasobnikAStav[index3]}}, s_{${stavKazdyKrok[index3]}} \\rangle`" style="display: inline-block;"/>

            
            <latex v-if="showFullLine[index3] && index3 < pocetKrokov" :latex-string="`\\alpha_{${index3}} = \\langle {${prvaCastPrekladu[index3] + ' : ' + druhaCastPrekladu[index3]}}, {${zasobnikAStav[index3]}}, s_{${stavKazdyKrok[index3]}} \\rangle \\Rightarrow \\alpha_{${index3+1}}`" style="display: inline-block;"/>
            <latex v-if="showFullLine[index3] && index3 >= pocetKrokov" :latex-string="`\\alpha_{${index3}} = \\langle {${prvaCastPrekladu[index3] + ' : ' + druhaCastPrekladu[index3]}}, {${zasobnikAStav[index3]}}, s_{${stavKazdyKrok[index3]}} \\rangle`" style="display: inline-block;"/>
          </span> 
        </div>
      </v-row>

      
      <v-dialog v-model="fullscreenDialog" hide-overlay transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="primary" flat>
            <v-btn icon dark @click="fullscreenDialog = false">
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Detailný výstup</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn dark @click="showStavy = !showStavy">Zobraziť Stavy</v-btn>
          </v-toolbar>
          <v-expand-transition>
              <v-card class="flex-shrink-0" v-if="showStavy" flat style="max-width: 400px; position: absolute; top: 64px; right:0; z-index: 5; border: 1px solid black">
                <v-card-title>Stavy</v-card-title>
                <v-table >
                  <tbody >
                    <tr v-for="(state,index6) in filtrovaneStavy" :key="index6">
                        <td v-if="index6 == 0" style="max-width:200px">
                          <latex :latex-string="`s_{${index6}} ${state}`"></latex>
                        </td>
                        <td v-if="index6 !== 0" style="max-width:200px">
                          <latex :latex-string="`s_{${index6}} = s_{${index6-1}} ${state}`"></latex>
                        </td>
                      </tr>
                  </tbody>
                </v-table>
              </v-card>
            </v-expand-transition>
          <v-card-text  style="height: calc(100% - 64px); overflow-y: auto;">
            <div class="output-row" v-for="index2 in Array.from({length: pocetKrokov+1}, (_, i) => i)" :key="index2">
              <span style="display: inline-block">
                <latex v-if="index2 < pocetKrokov" :latex-string="`\\alpha_{${index2}} = \\langle {${kroky[index2]}}, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}} \\rangle \\Rightarrow \\alpha_{${index2+1}}`" style="display: inline-block;"/>
                <latex v-if="index2 >= pocetKrokov" :latex-string="`\\alpha_{${index2}} = \\langle {${kroky[index2]}}, {${zasobnikAStav[index2]}}, s_{${stavKazdyKrok[index2]}} \\rangle`" style="display: inline-block;"/>
              </span> 
            </div>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-col>
  </v-row>
  <v-row class="ma-3 " v-if="kroky[0]" style="margin-top: 20px !important;">
    <h3>Preklad inštrukcií do inštrukcií abstraktného stroja:</h3>
    <span class="overflow" >
        <div v-for="(cast, index) in kroky[0].split(':')" :key="index" style="display: inline-block;">
          <latex :latex-string="`{${cast.trim()}}${index < kroky[0].split(':').length - 1 ? '\\: : \\:' : ''}`" style="display: inline-block;"/>
        </div>
      </span>
  </v-row>
</div> 
</template>

<script>
import MonacoEditor from './components/MonacoEditor.vue';
import Latex from './components/Latex.vue';
import html2canvas from 'html2canvas';


export default {
  name: 'App',
  components: {
    MonacoEditor,
    Latex
  },
  data() {
    return {
      stackDialog: false,
      stateDialog: false,
      screenWidth: window.innerWidth,
      fixedContent1: 'x := 1',
      fixedContent2: `x := 1;\ny := 4;\nwhile !(x = y) do (\n\tx := x * y;\n\ty := y - 1\n)`,
      fixedContent3: `if !(x = 1) then (\n    x := x + 1\n) else skip;\nif ¬(x = 1) then (\n    x := x + 1\n) else skip;`,
      fixedContent4: `if !(x = 1) and x <= 0 then x := x+1 else skip`,
      exportContent: 'content1',
      exportDialog: false,
      showFullLine: {},
      pocetInstrukciiPrvyKrok: [],
      prvaCastPrekladu: [],
      druhaCastPrekladu: [],
      showStack: false,
      showStates: false,
      editorValue:'',
      fontSizes: [12,18, 24, 36,48,72],
      selectedFontSize: 18,
      output: '',
      showOutput: false,
      showStepping: false,
      ast: null,
      variables: [],
      premenneNaZaciatku: [],
      variableName: '',
      variableValue: '',
      kroky: [],
      stavy: [],
      stav: [],
      stavCounter: 0,
      zasobnik: [],
      show: false,
      show2: false,
      show3: false,
      zasobnikShow: [],
      premenna: [],
      currentIndex: 0,
      showFullOutput: false,
      headers: [
        { text: 'Názov', value: 'name' },
        { text: 'Hodnota', value: 'value' }
      ],
      stavKazdyKrok: [],
      zasobnikAStav: [],
      alert: false,
      alert2: false,
      alert3: false,
      fullscreenDialog: false,
      showStavy: false,
      premenne: [],
      premenneFetch: [],
      showDialog: false,
      tab:null,
      instrukcieKrok: [],
      tabulkoveData: [],
      showPreviousSteps: false,
      content: 'content1',
      pocetKrokov: 0,
      abstraktnaImplementacia:' (c, st, s)',
      aritmetickeVyrazyHlava:`
      \\mathscr{T}\\mathscr{E} : \\textbf{Expr} \\rightarrow \\textbf{Code}
      `,
      aritmetickeVyrazy: `
        \\begin{align*}
          \\mathscr{T}\\mathscr{E}\\llbracket n \\rrbracket &= \\text{PUSH}-n \\\\
          \\mathscr{T}\\mathscr{E}\\llbracket x \\rrbracket &= \\text{FETCH}-x \\\\
          \\mathscr{T}\\mathscr{E}\\llbracket e_1 + e_2 \\rrbracket &= \\mathscr{T}\\mathscr{E}\\llbracket e_2 \\rrbracket : \\mathscr{T}\\mathscr{E}\\llbracket e_1 \\rrbracket : \\text{ADD} \\\\
          \\mathscr{T}\\mathscr{E}\\llbracket e_1 * e_2 \\rrbracket &= \\mathscr{T}\\mathscr{E}\\llbracket e_2 \\rrbracket : \\mathscr{T}\\mathscr{E}\\llbracket e_1 \\rrbracket : \\text{MULT} \\\\
          \\mathscr{T}\\mathscr{E}\\llbracket e_1 - e_2 \\rrbracket &= \\mathscr{T}\\mathscr{E}\\llbracket e_2 \\rrbracket : \\mathscr{T}\\mathscr{E}\\llbracket e_1 \\rrbracket : \\text{SUB}
        \\end{align*}
      `,
      boolovskeVyrazyHlava:`
      \\mathscr{T}\\mathscr{B} : \\textbf{Bexpr} \\rightarrow \\textbf{Code}
      `,
      boolovskeVyrazy: `
      \\begin{align*}
          \\mathscr{T}\\mathscr{B}\\llbracket true \\rrbracket &= \\text{TRUE}  \\\\
          \\mathscr{T}\\mathscr{B}\\llbracket false \\rrbracket &= \\text{FALSE} \\\\
          \\mathscr{T}\\mathscr{B}\\llbracket e_1 = e_2 \\rrbracket &= \\mathscr{T}\\mathscr{E}\\llbracket e_2 \\rrbracket : \\mathscr{T}\\mathscr{B}\\llbracket e_1 \\rrbracket : \\text{EQ} \\\\
          \\mathscr{T}\\mathscr{B}\\llbracket e_1 \\leq e_2 \\rrbracket &= \\mathscr{T}\\mathscr{E}\\llbracket e_2 \\rrbracket : \\mathscr{T}\\mathscr{E}\\llbracket e_1 \\rrbracket : \\text{LE}  \\\\
          \\mathscr{T}\\mathscr{B}\\llbracket e_1 \\neg e_2 \\\rrbracket &= \\mathscr{T}\\mathscr{B}\\llbracket b \\rrbracket : \\text{NEG} \\\\
          \\mathscr{T}\\mathscr{B}\\llbracket b_1 \\land b_2 \\rrbracket &= \\mathscr{T}\\mathscr{B}\\llbracket b_2 \\rrbracket : \\mathscr{T}\\mathscr{B}\\llbracket b_1 \\rrbracket : \\text{AND} 
      \\end{align*}
      `,
      prikazyHlava:`
      \\mathscr{T}\\mathscr{S} : \\textbf{Statm} \\rightarrow \\textbf{Code}
      `,
      prikazy: `
        \\begin{align*}
          \\mathscr{T}\\mathscr{S}\\llbracket x:=e \\rrbracket &= \\mathscr{T}\\mathscr{S}\\llbracket e \\rrbracket : \\text{STORE}-x \\\\
          \\mathscr{T}\\mathscr{S}\\llbracket \\text{skip} \\rrbracket &= \\text{EMPTYOP}  \\\\
          \\mathscr{T}\\mathscr{S}\\llbracket S_1;S_2\\rrbracket &= \\mathscr{T}\\mathscr{S}\\llbracket S_1 \\rrbracket : \\mathscr{T}\\mathscr{S}\\llbracket S_2 \\rrbracket  \\\\
          \\mathscr{T}\\mathscr{S}\\llbracket \\text{if}~b~\\text{then}~S_1~ \\text{else}~S_2~\\rrbracket &= \\mathscr{T}\\mathscr{B}\\llbracket b \\rrbracket : \\text{BRANCH}(\\mathscr{T}\\mathscr{S}\\llbracket S_1\\rrbracket, \\mathscr{T}\\mathscr{S}\\llbracket S_2 \\rrbracket) \\\\ 
          \\mathscr{T}\\mathscr{S}\\llbracket \\text{while}~b~ \\text{do}~S~ \\rrbracket &= \\text{LOOP}(\\mathscr{T}\\mathscr{B}\\llbracket b \\rrbracket, \\mathscr{T} \\mathscr{S} \\llbracket S \\rrbracket) 
        \\end{align*}
      `,
      push: `\\langle\\textbf{PUSH} - n : c, st, s\\rangle \\Rightarrow \\langle c, N \\llbracket n \\rrbracket : st, s\\rangle`,
      add: `\\langle\\textbf{ADD} : c, v_1 : v_2 : st, s\\rangle \\Rightarrow \\langle c, (v_1 \\oplus v_2) : st, s\\rangle`,
      mult: `\\langle\\textbf{MULT} : c, v_1 : v_2 : st, s\\rangle \\Rightarrow \\langle c,(v_1 \\otimes v_2) : st, s\\rangle`,
      sub: `\\langle\\textbf{SUB} : c, v_1 : v_2 : st, s\\rangle \\Rightarrow \\langle c, (v_1 \\ominus v_2) : st, s\\rangle`,
      eq: `\\langle\\textbf{EQ} : c, v_1 : v_2 : st, s\\rangle \\Rightarrow \\langle c, (v_1 = v_2) : st, s\\rangle`,
      le: `\\langle\\textbf{LE} : c, v_1 : v_2 : st, s\\rangle \\Rightarrow \\langle c, (v_1 \\leq v_2) : st, s\\rangle`,
      True: `\\langle\\textbf{TRUE} : c, st, s\\rangle \\Rightarrow \\langle c, \\textbf{tt} : st, s\\rangle`,
      False: `\\langle\\textbf{FALSE} : c, st, s\\rangle \\Rightarrow \\langle c, \\textbf{ff} : st, s\\rangle`,
      and: `\\langle\\textbf{AND} : c, t_1 : t_2 : st, s\\rangle \\Rightarrow 
          \\begin{cases} 
          \\langle c, \\textbf{tt} : st, s\\rangle, & \\text{ak } t_1 = \\textbf{tt} \\text{ a } t_2 = \\textbf{tt} \\\\
          \\langle c, \\textbf{ff} : st, s\\rangle, & \\text{ak } t_1 = \\textbf{ff} \\text{ alebo } t_2 = \\textbf{ff}
          \\end{cases}`,
      neg: `\\langle\\textbf{NEG} : c, t : st, s\\rangle \\Rightarrow 
              \\begin{cases} 
              \\langle c, \\textbf{tt} : st, s\\rangle, & \\text{ak } t = \\textbf{ff}\\\\
              \\langle c, \\textbf{ff} : st, s\\rangle, & \\text{ak } t = \\textbf{tt} 
              \\end{cases}`,
      fetch: `\\langle\\textbf{FETCH} - x : c, st, s\\rangle \\Rightarrow \\langle c, (s \ x) : st, s\\rangle`,
      store: `\\langle\\textbf{STORE} - x : c, v : st, s\\rangle \\Rightarrow \\langle c, st, s[ x \\mapsto v]\\rangle`,
      branch: `\\langle\\textbf{BRANCH}(c_1, c_2) : c, t : st, s\\rangle \\Rightarrow
            \\begin{cases} 
            \\langle c_1 : c, st, s\\rangle, & \\text{ak } t = \\textbf{tt} \\\\
            \\langle c_2 : c, st, s\\rangle, & \\text{ak } t = \\textbf{ff} 
            \\end{cases}`,
      loop: `\\begin{aligned}
      \\langle\\textbf{LOOP}(c_1, c_2) : c, st, s\\rangle &\\Rightarrow \\\\
              &\ \\langle c_1 : \\textbf{BRANCH}(c_2 : \\textbf{LOOP}(c_1, c_2), \\textbf{EMPTYOP}) : c, st, s\\rangle 
              \\end{aligned}`,
      emptyop: `\\langle\\textbf{EMPTYTOP} : c, st, s\\rangle \\Rightarrow \\langle c, st, s\\rangle `,
      pushText:`\\text{Inštrukcia } \\textbf{PUSH} - ~n~ \\text{zoberie hodnotu čísla} ~n~ \\text{a vloží ju na vrchol zásobníka:}`,
      addText:`\\text{Inštrukcie } \\textbf{ADD}, \\textbf{MULT}, \\textbf{SUB}, \\textbf{EQ}, \\textbf{LE} \\text{ očakávajú, že na vrchole zásobníka sa nachádzajú dve číselné hodnoty. \\ Pri inštrukciách } \\textbf{ADD}, \\textbf{MULT} \\text{ a } \\textbf{SUB} \\text{ sa tie dve číselné hodnoty odstránia zo zásobníka a výsledok danej operácie sa zapíše na vrchol zásobníka:}`,
      eqText:`\\text{Inštrukcia } \\textbf{EQ} \\text{ potom čo porovná dve číselné hodnoty tak ich odstráni zo zábosníka a na vrchol zapíše pravdivostnú hodnotu relácie rovnosti:}`,
      leText:`\\text{Inštrukcia } \\textbf{LE} \\text{ porovná dve hodnoty na vrchole zásobníka, odstráni ich a na vrch zapíše pravidvostnú hodnotu nerovnosti:}`,
      TrueText:`\\text{Inštrukcie }\\textbf{TRUE} \\text{ a } \\textbf{FALSE}\\text{ vložia na vrchol zásobníka pravdivostnú hodnotu} \\textbf{ tt } \\text{alebo} \\textbf{ ff}:`,
      andText:`\\text{Inštrukcia }\\textbf{AND } \\text{očakáva na vrchole zásbníka dve pravdivostné hodnoty, $t_1$ a $t_2$, vykoná ich konjukciu, odstráni ich a výslednú pravdivostnú hodnotu} \\textbf{ tt}\\text{ alebo }\\textbf{ff} \\text{ vloží na vrchol zásobníka:}`,
      negText:`\\text{Inštrukcia } \\textbf{NEG } \\text{očakáva na vrchole zásobníka pravdivostnú hodnotu, ktorú odstráni a na vrchol zapíše negáciu pravdivostnej hodnoty:}`,
      fetchText:`\\text{Inštrukcia }\\textbf{FETCH}-\\textit{x}\\text{ zoberie hodnotu premennej } \\textit{x } \\text{v danom stave a vloží ho na vrchol zásobníka:}`,
      storeText:`\\text{Inštrukcia }\\textbf{STORE}-\\textit{x } \\text{zoberie hodnotu z vrchola zásoníka } (\\textit{v}), \\text{ ktorá predpokladá, že je číselnou hodnotou, a priradí ju premennej } \\textit{x}:`,
      branchText:`\\text{Inštrukcia }\\textbf{BRANCH}(c_1, c_2) \\text{ očakáva na vrchole zásobníka 
      pravdivostnú hodnotu }(\\textit{t}), \\text{ podľa ktorej vykoná vetvenie programu. Ak teda bude pravdivostná hodnota pravdivá vykoná kód } c_1 \\text{ a ak nepravdivá tak vykoná kód }c_2:`,
      loopText:`\\text{Inštrukcia }\\textbf{LOOP}(c_1, c_2) \\text{ vykoná kód }c_1 \\text{ a potom vykoná inštrukciu }\\textbf{BRANCH}:`,
      emptyopText:`\\text{Inštrukcia } \\textbf{EMPTYOP} \\text{ je prázdna inštrukcia, ktorá nezmení ani zásobník, ani stav:}`
    };
  },
  computed:{
    filtrovaneStavy(){
      let krok = this.currentIndex;
      const pocetStavov = this.stavKazdyKrok[krok];
      return this.stavy.slice(0,pocetStavov);
    }
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    showStackDialog(){
      this.stackDialog = true;
    },
    showStateDialog(){
      this.stateDialog = true;
    },
    handleResize() {
      this.screenWidth = window.innerWidth;
    },
    showExportPopup(){
      this.exportDialog = true;
    },
    exportToImage() {
      this.$nextTick(() => {
        html2canvas(this.$refs.exportContent).then(canvas => {
          let link = document.createElement('a');
          link.download = 'export.png';
          link.href = canvas.toDataURL('image/png');
          link.click();
          link.delete;
        });
      });
    },
    scrollToBottom() {
      const container = document.querySelector('.output-container');
      if (container) {
        container.scrollTop = container.scrollHeight;
      }
    },
    toggleText(index) {
      
      if (this.showFullLine[index] === undefined) {
        this.showFullLine[index] = true; 
      } else {
        this.showFullLine[index] = !this.showFullLine[index];
      }
    },
    wrapKeywordsWithLatex(text) {
      const keywords = ["PUSH", "FETCH", "ADD", "MULT", "SUB", "TRUE", "FALSE", "EQ", "LE", "NEG", "AND", "STORE", "EMPTYOP", "BRANCH", "LOOP"];
      const regex = new RegExp(`\\b(${keywords.join('|')})\\b`, 'g');

      text = text.replace(regex, '\\textbf{$1}');
      text = text.replace(/:/g, ' : ');
      text = text.replace(/\(/g, ' ( ');
      text = text.replace(/\)/g, ' ) ');
      text = text.replace(/,/g, ' , ');

      const parts = text.split(' : ');

      if (parts.length > 4) {
        this.pocetInstrukciiPrvyKrok.push(parts.length);
        this.prvaCastPrekladu.push(parts.slice(0, 4).join(' : '));
        this.druhaCastPrekladu.push(parts.slice(4).join(' : '));
        return parts.join(' : ');
      }
      this.pocetInstrukciiPrvyKrok.push(parts.length);
      this.prvaCastPrekladu.push(parts.join(' : '));
      this.druhaCastPrekladu.push("0");
      return parts.join(' : ');
    },
    updateFontSize(value){
      this.selectedFontSize = value;
    },
    ziskaniePremennych(strom){
      let regex = /FETCH - ([^:()]+)/g;
      let match;
      this.premenneFetch = [];
      while ((match = regex.exec(strom)) !== null) {
        let variable = match[1].trim();
        if(!this.premenneFetch.includes(variable))
          this.premenneFetch.push(variable);
      }
    },
    handleAst(newAst){
      this.ast = newAst;
      this.ziskaniePremennych(this.ast);
    },
    displayOutput() {
      this.updateInitialState();
      this.kroky = [];
      this.stavCounter = 0;
      this.zasobnik = [];
      this.zasobnikShow = [];
      this.output = this.ast; 
      this.showStepping = false
      this.stavKazdyKrok = [];
      this.zasobnikAStav = [];
      this.pocetKrokov = 0;
      this.show2 = false;
      this.alert2 = false;
      this.tabulkoveData = [];
      this.instrukcieKrok =[];
      this.showFullLine = {};
      this.pocetInstrukciiPrvyKrok = [];
      this.prvaCastPrekladu = [];
      this.druhaCastPrekladu = [];
      if(this.output != null){ 
        let vysledok = this.vyhodnotenieVyrazu();
        if(this.pocetKrokov > 1000){
          this.show3 = true;
          this.alert3 = true;
        }else{
          this.show3 = false;
          this.alert3 = false;
          if(vysledok === 0){
            this.show2 = false;
            this.alert2 = false;
            this.instrukcieKrok.push('\u03B5');
            this.enableStepping();
            this.showOutput = true;
            this.variables = this.premenneNaZaciatku.map(item => ({ ...item }));
          }else {
            this.show2 = true;
            this.alert2 = true;
          }
        }
      }
    },
    enableStepping() {
      this.showStepping = true; 
      this.showFullOutput = false; 
      this.currentIndex = 0;
    },
    hideStepping() {
      this.showStepping = false;
      this.showFullOutput = true;
      this.currentIndex = this.kroky.length -1;
      this.showStack = false;
    },
    nextStep() {
      if (this.currentIndex < this.kroky.length - 1) {
        this.currentIndex++;
        this.scrollToBottom();
      }
    },
    previousStep() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
    updateInitialState() {
      this.stavy = [];
      this.variables = this.variables.filter(variable => variable.name.trim() && variable.value.trim());
      const stateString = this.variables.map((variable, index, array) => {
        let variableText = `${variable.name} \\mapsto ${variable.value}`;
        if (index < array.length - 1) {
          variableText += ',';
        }
        if (index % 2 === 1 && index !== array.length - 1) 
          variableText += ' \\\\'; 
        return variableText;
      }).join(''); 
      if(stateString === ''){
        this.stavy[0] = `[ ]`;
      }
      else{
        this.stavy[0] = `[${stateString}]`;
      }
        
    },
    addOrUpdateVariable(name, value) {
      const varIndex = this.variables.findIndex(v => v.name === name);
      if (varIndex !== -1) {
        this.variables[varIndex].value = value;
      } else {
        this.variables.push({ name, value });
      }
    },
    addVariable() {
      if(isNaN(this.variableValue)){
        this.show = true;
        this.alert = true;
      } else if (this.variableName.trim() && this.variableValue.trim()) {
        const existingVariableIndex = this.variables.findIndex(v => v.name === this.variableName.trim());

        if (existingVariableIndex !== -1) {
          this.variables[existingVariableIndex].value = this.variableValue.trim();
          if(this.stavCounter === 0){
            this.addOrUpdateVariable(this.variableName.trim(),this.variableValue.trim());
          }
        } else {
          this.variables.push({
            name: this.variableName.trim(),
            value: this.variableValue.trim()
          });
          }
        }
        this.variableName = '';
        this.variableValue = '';
        this.show = false;
        this.alert = false;
        this.show2 = false;
        this.alert2 = false;
    },
    addVariable2() {
      if(isNaN(this.variableValue)){
        this.show = true;
        this.alert = true;
      } else if (this.variableName.trim() && this.variableValue.trim()) {
        const existingVariableIndex = this.premenneNaZaciatku.findIndex(v => v.name === this.variableName.trim());

        if (existingVariableIndex !== -1) {
          this.premenneNaZaciatku[existingVariableIndex].value = this.variableValue.trim();
          if(this.stavCounter === 0){
            const varIndex = this.premenneNaZaciatku.findIndex(v => v.name === name);
            if (varIndex !== -1) {
              this.premenneNaZaciatku[varIndex].value = value;
            } else {
              this.premenneNaZaciatku.push({name : this.variableName.trim(), value :this.variableValue.trim() });
            }
          }
        } else {
          this.premenneNaZaciatku.push({
            name: this.variableName.trim(),
            value: this.variableValue.trim()
          });
        }
        this.variableName = '';
        this.variableValue = '';
        this.show = false;
        this.alert = false;
        this.show2 = false;
        this.alert2 = false;
        this.variables = this.premenneNaZaciatku.map(item => ({ ...item }));
      }
    },
    vyhodnotenieVyrazu() {
      let zasAStav = `\u03B5 `.trim();
      let krok = `${this.output}`.trim();
      krok = this.wrapKeywordsWithLatex(krok);
      this.kroky.push(krok);
      this.zasobnikAStav.push(zasAStav);
      this.stavKazdyKrok.push(this.stavCounter+1);
      this.premenna = this.splitCommands(this.output);
      while (this.premenna.length > 0) {
        this.pocetKrokov++;
        if(this.pocetKrokov > 1000){
          break;
        }
        const command = this.premenna.shift(); 
        let vysledok = this.spracovanie(command);
        if(vysledok === 1) return 1;
        if(this.zasobnikShow.length === 0){
          if(this.premenna.length ===0 ){
            krok = `\u03B5`.trim();
            zasAStav = `\u03B5 `.trim();
          }else{
            krok = `${this.premenna.join(' : ')}`.trim();
            zasAStav = `\u03B5 `.trim();
          }
        }else{
          let zas = this.zasobnikShow.slice().reverse().join(' : ');
          krok = `${this.premenna.join(' : ')}`.trim();
          zasAStav = `${zas} `.trim();
        }
        if (this.premenna.length === 0 && this.zasobnikShow.length > 0) {
          let zas = this.zasobnikShow.reverse().join(' : ');
          krok = `\u03B5`.trim();
          zasAStav = `${zas} `.trim();
        }
        this.stavKazdyKrok.push(this.stavCounter+1);
        krok = this.wrapKeywordsWithLatex(krok);
        this.kroky.push(krok); 
        this.zasobnikAStav.push(zasAStav);
      }
      return 0;
    },
    spracovanie(command) {
      let parts = 0;
      let operation = 0;
      let variable = 0;
      if(command.startsWith('LOOP')) {
        operation = 'LOOP';
        this.instrukcieKrok.push(operation);
      }else if(command.startsWith('BRANCH')){
        operation = 'BRANCH';
        this.instrukcieKrok.push(operation);
      } 
      else {
        this.instrukcieKrok.push(command);
        parts = command.split(' - ');
        operation = parts[0];
        variable = parts.length > 1 ? parts[1] : null;
      }
      let cislo1 = 0;
      let cislo2 = 0;
      let vysledok = 0;
      switch (operation){
        case 'FETCH':
          let variableEntry = this.variables.find(v => v.name === variable);
          let value = 0;
          if (!variableEntry) {
            return 1;
          } else{
            value = variableEntry.value;
          }
          this.zasobnik.push(value);
          this.zasobnikShow.push(value);
          break;
        case 'PUSH':
          this.zasobnik.push(Number(variable));
          this.zasobnikShow.push(Number(variable));
          break;
        case 'ADD':
          cislo1 = this.zasobnik.pop();
          cislo2 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          this.zasobnikShow.pop();
          vysledok = Number(cislo1) + Number(cislo2);
          this.zasobnik.push(vysledok);
          //this.zasobnikShow = [];
          this.zasobnikShow.push(vysledok);
          break;
        case 'MULT':
          cislo1 = this.zasobnik.pop();
          cislo2 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          this.zasobnikShow.pop();
          vysledok = Number(cislo1) * Number(cislo2);
          this.zasobnik.push(vysledok);
          this.zasobnikShow.push(vysledok);
          break;
        case 'SUB':
          cislo1 = this.zasobnik.pop();
          cislo2 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          this.zasobnikShow.pop();
          vysledok = Number(cislo1) - Number(cislo2);
          this.zasobnik.push(vysledok);
          //this.zasobnikShow = [];
          this.zasobnikShow.push(vysledok);
          break;
        case 'TRUE':
          this.zasobnik.push("tt");
          //this.zasobnikShow = [];
          this.zasobnikShow.push("tt");
          break;
        case 'FALSE':
          this.zasobnik.push("ff");
          //this.zasobnikShow = [];
          this.zasobnikShow.push("ff");
          break;
        case 'EQ':
          cislo1 = this.zasobnik.pop();
          cislo2 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          this.zasobnikShow.pop();
          if(Number(cislo1) === Number(cislo2)){
            this.zasobnik.push("tt");
            this.zasobnikShow.push("tt");
          }
          else{
            this.zasobnik.push("ff");
            this.zasobnikShow.push("ff");
          } 
          break;
        case 'LE':
          cislo1 = this.zasobnik.pop();
          cislo2 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          this.zasobnikShow.pop();
          if(cislo1 <= cislo2){
            this.zasobnik.push("tt");
            this.zasobnikShow.push("tt");
          }
          else{
            this.zasobnik.push("ff");
            this.zasobnikShow.push("ff");
          } 
          break;
        case 'NEG':
          cislo1 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          if(cislo1 === "ff"){
            this.zasobnik.push("tt");
            this.zasobnikShow.push("tt");
          }
          else {
            this.zasobnik.push("ff");
            this.zasobnikShow.push("ff");
          }
          break;
        case 'AND':
          cislo1 = this.zasobnik.pop();
          cislo2 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          this.zasobnikShow.pop();
          if(cislo1 === "tt" && cislo2 === "tt"){
            this.zasobnik.push("tt");
            //this.zasobnikShow = [];
            this.zasobnikShow.push("tt");
          }
          else {
            this.zasobnik.push("ff");
            //this.zasobnikShow = [];
            this.zasobnikShow.push("ff");
          }
          break;
        case 'STORE':
          this.stavCounter++;
          cislo1 = this.zasobnik.pop();
          this.zasobnikShow.pop();
          //this.zasobnikShow = [];
          this.variableName = variable;
          this.variableValue = cislo1.toString();
          const stateString = `[${variable} \\mapsto ${cislo1}]`;
          this.stavy[this.stavCounter] = stateString;
          this.stav = [];
          this.addVariable();
          break;
        case 'EMPTYOP':
          break;       
          case 'BRANCH':
            cislo1 = this.zasobnik.pop();
            this.zasobnikShow.pop();
            let [telo1, telo2] = this.splitBranchContent(command);
            let daco = cislo1 === "tt" ? telo1.trimStart() : telo2.trimStart();
            let noveInstrukcie = this.splitCommands(daco);
            if(this.premenna.length === 0){
              this.premenna = noveInstrukcie;
            } else {
              
              this.premenna.unshift(...noveInstrukcie); 
            }
            break;
        case 'LOOP':     
          let [podmienka, telo] = this.splitLoopContent(command);
          let cyklus = `${podmienka.trimStart()} : BRANCH(${telo}: ${command}, EMPTYOP)`;
          let idk = this.splitCommands(cyklus);
          if(this.premenna.length === 0 ){
            this.premenna = idk;
          }else{
            this.premenna.unshift(...idk);
          }
          break;         
      }
      return 0;
    },
    splitCommands(input) {
      let commands = [];
      let currentCommand = '';
      let depth = 0;

      for (let i = 0; i < input.length; i++) {
        const char = input[i];

        if (char === '(') depth++;
        else if (char === ')') depth--;

        if (char === ':' && depth === 0) {
          // Našli sme dvojbodku mimo zátvoriek, čas pridať príkaz
          commands.push(currentCommand.trim());
          currentCommand = '';
        } else {
          currentCommand += char;
        }

        // Ak sme na konci reťazca, pridajme zostávajúci príkaz
        if (i === input.length - 1 && currentCommand) {
          commands.push(currentCommand.trim());
        }
      }

      return commands;
    },
    splitLoopContent(loopString) {
      let depth = 0; 
      let parts = ['', ''];
      let partIndex = 0;

      let i = loopString.indexOf('LOOP(') + 'LOOP('.length;

      for (; i < loopString.length; i++) {
        let char = loopString[i];

        if (char === '(') depth++;
        else if (char === ')') depth--;

        if (char === ',' && depth === 0) {
          partIndex = 1;
          continue;
        }

        parts[partIndex] += char;

        if (depth === -1) {
          break;
        }
      }

      parts[1] = parts[1].slice(0, -1);

      return parts;
    },
    splitBranchContent(branchString) {
      let depth = 0; 
      let parts = ['', ''];
      let partIndex = 0;

      let i = branchString.indexOf('BRANCH(') + 'BRANCH('.length;

      for (; i < branchString.length; i++) {
        let char = branchString[i];

        if (char === '(') depth++;
        else if (char === ')') depth--;

        if (char === ',' && depth === 0) {
          partIndex = 1;
          continue;
        }

        parts[partIndex] += char;

        if (depth === -1) {
          break;
        }
      }

      parts[1] = parts[1].slice(0, -1);

      return parts;
    },
    pasteExample(){
      this.premenneNaZaciatku = [];
      let variableName = 'x';
      let variableValue = '4';
      this.premenneNaZaciatku.push({
            name: variableName.trim(),
            value: variableValue.trim()
      });
      this.variables = this.premenneNaZaciatku.map(item => ({ ...item }));
      this.updateInitialState();
      this.editorValue = 
        `i := 1;
while !(x = 1) do (
    i := i*x;
    x := x-1
)`;
    },
    triggerFileInput() {
      this.$refs.fileInput.click(); 
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file && file.type === "text/plain") {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.editorValue = e.target.result; 
          console.log(this.editorValue);
        };
        reader.readAsText(file);
        console.log(this.editorValue);
      } else {
        
      }
    }
  }
};
</script>